<template>
<div>
	<div v-if="dataCall !== null" class="contentCall">
		<div class="logo">
			<img :src="imgUrl" alt="User Logo">
		</div>
		<div class="infoCall">
			<h3>{{dataCall.contact.name}}</h3>
			<h4 v-if="dataCall.contact.phone === dataCall.call.attributes.number || !findLocal">{{dataCall.contact.phone}}</h4>
			<h4 v-else-if="dataCall.contact.mobile === dataCall.call.attributes.number">{{dataCall.contact.mobile}}</h4>
			<p v-if="!findLocal">Not in the contact list</p>
		</div>
		<div class="actionCall">
			<button type="button" class="btn btn-success" v-if="dataCall.call.attributes.state === 'ring' && dataCall.call.attributes.direction === 'incoming'" @click="acceptCall($event, {id: dataCall.call.id, contact: dataCall.contact })
        ">
          <span class="glyphicon glyphicon-earphone" aria-hidden="true"></span>
        </button>
			<button type="button" class="btn btn-danger" @click="declineCall(dataCall.call.id)
        ">
          <span class="glyphicon glyphicon-earphone" aria-hidden="true"></span>
        </button>
		</div>
	</div>
	<div v-else>
		<h4>Now there are no calls</h4>
	</div>
</div>
</template>

<script>
import wiservice from 'models/wiservice'
import { getImgUrl } from '@/utils/helper'

export default {
	name: 'call-contact',

	props: {
		idCall: {
			type: String,
			required: true
		},
		find: {
			type: Boolean,
			required: true
		}
	},

	data() {
		return {
			findLocal: this.find
		}
	},

	computed: {
		client() {
			return this.$store.getters['client'];
		},
		dataCall() {
			return this.calls.find(item => item.call.id === this.$props.idCall) || null;
		},
		calls() {
			return this.$store.getters['globalPopup/dataCalls'];
		},
		domain() {
			return this.$store.getters['domain'];
		},
		imgUrl() {
			return getImgUrl('user.png');
		}
	},

	methods: {
		async acceptCall(e, option) {
			e.target.tagName === 'SPAN' ? e.target.parentNode.style.display = 'none' : e.target.style.display = 'none';
			wiservice.answer(option.id);

			if (typeof option.contact !== 'undefined' && typeof option.contact.id !== 'undefined') {
				window.open(`https://${this.domain}/a/contacts/${option.contact.id}${process.env.NODE_ENV === 'production' ? '' : '?dev=true'}`);
			} else {
				const params = {
					name: option.contact.name,
					phone: option.contact.phone
				};
				await this.$store.dispatch('openModalContact', params);
			}
		},
		declineCall(id) {
			wiservice.hangup(id);
			this.$router.push({ name: 'defGlobalSidebar' });
		}
	}
}
</script>

<style scoped>
.contentCall,
.infoCall {
	display: flex;
	align-items: center;
	justify-content: space-around;
	flex-direction: column;
}

.actionCall {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.actionCall>button {
	margin: 0 4px;
}

.logo>img {
	width: 120px;
}

.glyphicon {
	font-size: 21px;
}

.actionCall button:last-child>span {
	transform: rotate(135deg)
}
</style>
