import * as types from './mutation-types'
import store from './index.js'
import router from '../router'
import wiservice from 'models/wiservice'
import { msgCall } from '@/utils/helper'

export default {
	[types.UPDATE_CLIENT](state, payload) {
		state.client = payload;
	},

	[types.UPDATE_CONTACT_INFO](state, payload) {
		state.contactInfo = payload;
	},

	[types.UPDATE_DOMAIN](state, payload) {
		state.domain = payload;
	},

	[types.UPDATE_USER](state, payload) {
		state.user = payload;
	},

	[types.WRITE_CONTEXT](state, payload) {
		state.contextApp = payload;
	},

	[types.WRITE_ERROR_CONTACT](state, payload) {
		state.errorCreateContact = payload;
	},

	async [types.GET_DATA_CONTACT](state, option) {
		// need refactoring
		if (option.type === 'newCall') {
			const callAtr = option.call.attributes;
			const contact = option.response.find(item => (item.phone === callAtr.number || item.mobile === callAtr.number));
			if (typeof contact !== 'undefined') {
				store.commit('globalPopup/addDataCalls', {
					contact: contact,
					call: option.call,
					find: true
				});
				store.commit(types.SHOW_NOTIFICATION, {
					type: 'success',
					message: msgCall(callAtr.direction, contact.name, callAtr.number === contact.phone ? contact.phone : contact.mobile)
				});
				await store.dispatch('triggerCtiSidebar', 'show');
				router.push({
					name: 'callContact',
					params: {
						idCall: option.call.id,
						find: true
					}
				});
			} else {
				const collaborationContact = {
					name: callAtr.name,
					phone: callAtr.number
				};
				store.commit('globalPopup/addDataCalls', {
					contact: collaborationContact,
					call: option.call,
					find: false
				});
				store.commit(types.SHOW_NOTIFICATION, {
					type: 'success',
					message: msgCall(callAtr.direction, collaborationContact.name, collaborationContact.phone)
				});

				await store.dispatch('triggerCtiSidebar', 'show');

				router.push({
					name: 'callContact',
					params: {
						idCall: option.call.id,
						find: false
					}
				});
			}
		}
	},

	[types.SHOW_NOTIFICATION](state, option) {
		if (state.client && state.client.interface) {
			state.client.interface.trigger('showNotify', {
				type: option.type,
				message: option.message
			})
		}
	},

	[types.ERROR_CREATE_CONTACT](state, e) {
		const error = JSON.parse(e.response);
		error.status = e.status;
		store.commit(types.WRITE_ERROR_CONTACT, error);
	},

	[types.MESSAGE_OTHER_INSTANCE](state) {
		state.client.instance.receive(function(event) {
			const data = event.helper.getData();
			if (data.message.type === 'call') {
				wiservice.call(data.message.phone);
			}
		}
		)
	}
}
