const getClient = () => {
	return new Promise((resolve, reject) => {
		window.app.initialized().then((client) => {
			if (client) {
				resolve(client)
			}
		}).catch((e) => {
			reject(new Error(e))
		})
	}).then(v => v)
}
class Client {
	constructor(client) {
		if (typeof client === 'undefined') {
			throw new Error('Client cannot be called directly')
		}
		this.client = client
	}
	static async build() {
		try {
			const client = await getClient()
			return new Client(client) || null
		} catch (e) {
			console.error(e)
		}
	}
}
export { Client }
