import * as types from './mutation-types'
import { Client } from 'models/client'
// import router from '../router'
// import store from "./index";

const headers = {
	'Authorization': 'Basic <%= encode(iparam.apiKey) %>',
	'Content-Type': 'application/json'
};

export const initContactModal = async ({ dispatch, state }) => {
	try {
		await dispatch('buildClient');
		await dispatch('getContextApp');
	} catch (e) {
		console.error(e);
	}
};

export const initCtiSidebar = async ({ dispatch, state, commit }) => {
	try {
		await dispatch('buildClient');
		commit(types.MESSAGE_OTHER_INSTANCE);
		// await dispatch('getData', { client, key: 'domainName', mutationType: 'UPDATE_DOMAIN' })
	} catch (e) {
		console.error(e);
	}
};

export const buildClient = async ({ dispatch, commit }) => {
	try {
		const client = await Client.build();
		commit(types.UPDATE_CLIENT, client.client);
		await dispatch('getData', { key: 'domainName', mutationType: 'UPDATE_DOMAIN' });
	} catch (e) {
		console.error(e);
	}
};

export const getData = async ({ commit, state }, option) => {
	state.client.data.get(option.key).then((data) => {
		commit(types[option.mutationType], data[option.key]);
	}).catch((e) => {
		console.error(e);
	})
};

export const getContactBy = async ({ commit, state }, option) => {
	if (state.domain !== null) {
		const optionsRequest = {
			headers
		};

		try {
			const url = `https://${state.domain}/api/v2/contacts?${option.by}=${option.value}`;
			const val = await state.client.request.get(url, optionsRequest);
			const response = JSON.parse(val.response);
			const dataMutation = {
				response,
				call: option.call || null,
				type: option.type
			};

			if (response.length === 0) {
				return 'not found';
			}
			commit(types.GET_DATA_CONTACT, dataMutation);
			return 'found';
		} catch (e) {
			console.error(e);
			if (e.status === 401) {
				commit(types.SHOW_NOTIFICATION, { type: 'danger', message: `Your API key is not valid` });
			}
		}
	}
};

export const triggerCtiSidebar = async ({ state }, value) => {
	try {
		await state.client.interface.trigger(value, { id: 'softphone' });
	} catch (e) {
		console.error(e);
	}
};

export const createContact = async ({ state, commit }, contactData) => {
	try {
		const url = `https://${state.domain}/api/v2/contacts`;
		const optionsRequest = {
			headers,
			body: JSON.stringify(contactData || {})
		};
		const data = await state.client.request.post(url, optionsRequest);
		const newContact = JSON.parse(data.response);

		commit(types.SHOW_NOTIFICATION, { type: 'success', message: `Contact ${newContact.name} created!` });
		await window.open(`https://${state.domain}/a/contacts/${newContact.id}${process.env.NODE_ENV === 'production' ? '' : '?dev=true'}`);
		state.client.instance.close();
	} catch (e) {
		commit(types.ERROR_CREATE_CONTACT, e);
	}
};

export const getContextApp = async ({ state, commit }, contactData) => {
	try {
		const context = await state.client.instance.context();

		commit(types.WRITE_CONTEXT, context);
	} catch (e) {
		console.error(e);
	}
};

export const openModalContact = async ({ state, commit, getters }, params) => {
	params.lng = window.loadLocaleData.getData('language')
	params.timeZone = window.loadLocaleData.getData('timezone')
	try {
		await state.client.interface.trigger('showModal', {
			title: 'New contact',
			template: '../contactModal/index.html',
			data: params
		});
	} catch (e) {
		console.error(e);
	}
};
