// import rootState from '../index'

export default {
	namespaced: true,

	state: {
		dataCalls: [],
		language: [],
		timeZone: []
	},

	getters: {
		dataCalls(state) { return state.dataCalls },
		dataLng(state) { return state.language },
		dataTimeZone(state) { return state.timeZone }
	},

	mutations: {
		addDataCalls(state, payload) {
			state.dataCalls.push(payload);
		},
		removeDataCalls(state, id) {
			state.dataCalls = state.dataCalls.filter(item => item.call.id !== id);
		},
		updateDataCalls(state, data) {
			state.dataCalls = state.dataCalls.filter(item => {
				if (item.call.id === data.call.id) {
					item = data;
				}
				return true;
			})
		},
		writeLocaleData(state, payload) {
			try {
				state[payload.key] = payload.data;
			} catch (e) {
				console.error(e);
				state[payload.key] = [];
			}
		}
	}
}
