<template>
<div class="wildixPopup">
	<img class="wildixLogo" :src="imgUrl" alt="Wildix Logo">
</div>
</template>

<script>
import wiservice from 'models/wiservice'
import { getImgUrl } from '@/utils/helper'

export default {
	name: 'default',

	computed: {
		imgUrl() {
			return getImgUrl('wildix-logo-2.png');
		}
	},

	methods: {
		checkWiserviceConnect() {
			if (wiservice.checkConnect()) {
				this.$store.commit('SHOW_NOTIFICATION', { type: 'success', message: 'Collaboration connected' });
				return false;
			}

			this.$store.commit('SHOW_NOTIFICATION', { type: 'warning', message: 'Collaboration connection lost' });
		}
	}
}
</script>

<style scoped>
.wildixPopup {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}

.wildixLogo {
	width: 360px;
}

button {
	text-transform: uppercase;
}
</style>
