import store from '../store'
import packageApp from '../../package.json'
import router from '../router'
const integration = new window.Wildix.IntegrationService({
	name: 'Freshdesk integration',
	version: packageApp.version || '0.0.0'
})
integration.Telephony.subscribe()
class ControllerWIService {
	constructor(wiservice) {
		this.wiservice = wiservice
		this.connectEventsList = ['connected', 'disconnected', 'error']
		this.telEventsList = ['ready', 'add', 'remove', 'answerEvent']
		this.init(this.connectEventsList)
		this.init(this.telEventsList)
	}
	ready() {
		this.wiservice.on('ready', () => {
			const status = this.wiservice.getCollaborationStatus()
			if (status === 'disconnected') {
				console.warn('Collaboration not running')
			}
			console.log('collaboration status', status)
			console.log('collaboration ready', this.wiservice.isReady())
			console.log('collaboration is connected', this.wiservice.isConnected())
		})
	}
	checkConnect() {
		return (this.wiservice.isReady() && this.wiservice.isConnected())
	}
	init(eventsList) {
		eventsList.forEach(fn => {
			this[fn]()
		})
	}
	connected() {
		this.wiservice.on('collaboration:connected', function(e) {
			console.log('CONNECTED', e)
			store.commit('SHOW_NOTIFICATION', { type: 'success', message: 'Collaboration connected' })
			console.log('Wildix app mode', process.env.NODE_ENV)
		})
	}
	disconnected() {
		this.wiservice.on('collaboration:disconnected', e => {
			store.commit('SHOW_NOTIFICATION', { type: 'warning', message: 'Collaboration disconnected' })
		})
	}
	error() {
		this.wiservice.on('connection:error', e => {
			store.commit('SHOW_NOTIFICATION', { type: 'error', message: 'Collaboration error' })
		})
	}
	answerEvent() {
		this.wiservice.Telephony.on('answer', function(call) {
			call.id = call.channel
			store.commit('globalPopup/updateDataCalls', { call })
		})
	}
	remove() {
		this.wiservice.Telephony.on('remove', call => {
			router.push({ name: 'defGlobalSidebar' })
			store.commit('SHOW_NOTIFICATION', { type: 'warning', message: 'Call removed' })
			store.commit('globalPopup/removeDataCalls', call.id)
		})
	}
	add() {
		this.wiservice.Telephony.on('add', async event => {
			console.log('Add call event')
			const getContactOption = {
				value: event.attributes.number,
				by: 'phone',
				type: 'newCall',
				call: event
			}
			try {
				const contactPhone = await store.dispatch('getContactBy', getContactOption)
				if (contactPhone === 'not found') {
					getContactOption.by = 'mobile'
					const contactMobile = await store.dispatch('getContactBy', getContactOption)
					if (contactMobile === 'not found') {
						const data = {
							response: [],
							call: event,
							type: 'newCall'
						}
						store.commit('GET_DATA_CONTACT', data)
					}
				}
			} catch (e) {
				console.error(e)
			}
		})
	}
	call(number) {
		if (number) {
			number = number.trim().replace(/[.,!?^${}()|\-/ig[\]\\]/g, '').replace(/\s+/g, '')
			try {
				if (this.isValidNumber(number)) {
					this.wiservice.Telephony.call(number)
					return false
				}
				// store.commit('SHOW_NOTIFICATION', { type: 'warning', message: 'Number for call in not validation' })
			} catch (e) {
				console.error(e)
			}
		}
	}
	answer(id = '') {
		this.wiservice.Telephony.answer(id)
	}
	hangup(id = '') {
		this.wiservice.Telephony.hangup(id)
	}
	isValidNumber(number) {
		const specialChars = {
			p: number.indexOf('+'),
			s: number.indexOf('#')
		}
		if (specialChars.p !== -1 || specialChars.s !== -1) {
			number = number.replace('+', '')
			number = number.replace('#', '')
		}
		if (!isNaN(parseInt(number))) {
			return true
		}
		console.log(number, specialChars)
		return false
	}
}
export default new ControllerWIService(integration)
